/*------------------------------------*\
  #ASPECT-RATIO
\*------------------------------------*/
.o-ar {
  display: block;
  overflow: hidden;
  position: relative;
  z-index: 1; }
  .o-ar, .o-ar:after {
    width: 100%; }
  .o-ar:after {
    content: "";
    display: block;
    height: 0; }
  .o-ar--1-1:after {
    padding-bottom: 100%; }

