\*------------------------------------*/
.o-grid {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 1760px;
  padding-right: 10px;
  padding-left: 10px; }

/*------------------------------------*\
  #IMAGE
\*------------------------------------*/
.o-img {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: -2; }
  .o-img--contain {
    object-fit: contain; }

/*------------------------------------*\
  #ROW
\*------------------------------------*/
.o-row {
  width: 100%; }

/**
 * Components
 */
/*------------------------------------*\
  #ARCHIVE
\*------------------------------------*/
.c-archive {
  position: relative; }
  .c-archive__icon {
    display: block;
    height: auto;
    width: 200px; }
  .c-archive__overlay {
    align-items: center;
    background: #4062a3;
    display: flex;
    height: 100%;
    justify-content: center;
    left: 0;
    opacity: 0;
    padding: 20px;
    pointer-events: none;
    position: fixed;
    top: 0;
    transition: opacity 0.3s ease-in-out, visibility 0s ease-in-out 0.3s;
    width: 100%;
    visibility: hidden;
    z-index: 9; }
    .c-archive__overlay.is-visible {
      opacity: 1;
      pointer-events: auto;
      transition-delay: 0s, 0s;
      visibility: visible; }

/*------------------------------------*\
  #BUTTON
\*------------------------------------*/
.c-btn {
  background: #ef5350;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  display: block;
  min-width: 100px;
  opacity: 1;
  padding: 10px;
  text-align: center;
  text-decoration: none;
  transition: background 0.3s ease-in-out, opacity 0.3s ease-in-out; }
  .c-btn:hover, .c-btn.is-active, .c-btn[disabled] {
    background: #c62828; }
  .c-btn[disabled] {
    opacity: 0.5; }

/*------------------------------------*\
  #CARD
\*------------------------------------*/
.c-card {
  background: #4062a3;
  margin-bottom: 20px;
  padding: 10px;
  transition: background 0.3s ease-in-out; }
  .c-card, .c-card__img {
    display: block;
    border-radius: 5px; }
  .c-card:hover {
    background: #ffc91b; }
  .c-card__img {
    background: #fff;
    height: 100%;
    object-fit: contain;
    padding: 20px;
    width: 100%; }

/*------------------------------------*\
  #LIST
\*------------------------------------*/
.c-list {
  align-items: center;
  display: flex;
  list-style: none;
  margin-left: -10px;
  width: calc(100% + 20px); }
  .c-list--right {
    justify-content: flex-end; }
  .c-list__item {
    margin-right: 10px;
    margin-left: 10px; }

